<template>
    <div>
      <mt-field label="" placeholder="" v-model="username"></mt-field>
      <mt-field label="" placeholder="" type="tel" v-model="phone"></mt-field>
      <mt-field label="QQ" placeholder="QQ" type="text" v-model="number"></mt-field>
      <mt-field label="" placeholder="" type="password" v-model="password"></mt-field>

      <mt-radio
        title=''
        v-model="course"
        :options="['H5', 'Java', 'UI']">
      </mt-radio>

      <div class="listen" @click="sub"></div>
    </div>
</template>
<style>

</style>
<script>
  import { MessageBox } from 'mint-ui';
    export default{
      data(){
        return{
          username:'',
          phone:'',
          number:'',
          password:'',
          course:'',
        }
      },
      methods:{
        sub(){
            if(this.username && this.phone && this.number && this.password && this.course){
              MessageBox.alert('!')
            }else {
              MessageBox.alert('!')
            }
        }
      }
    }
</script>



