<template>
  <div>
    <div class="nuser">
      <div class="icon user"></div>
      <mt-field placeholder="" v-model="phone" class="line"></mt-field>
    </div>
    <div class="npass">
      <div class="icon pasw">  </div>
      <mt-field  placeholder="" type="password" v-model="password" class="line"></mt-field>
    </div>
    <div class="forget">?</div>
    <div class="login" @click="login"></div>
    <div class="none"><router-link :to="{path:'/Register'}"></router-link></div>
    <div class="third">
      <span></span>
      <span></span>
      <span></span>
    </div>
  </div>
</template>
<style>

</style>
<script>
  import { MessageBox } from 'mint-ui';
  export default{
    data(){
      return{
        phone:'',
        password:''
      }
    },
    methods:{
      login(){
        if(this.$store.state.people[this.phone] == this.password){
          MessageBox.alert('!');
          this.$router.push('/about')
        }else {
          MessageBox.alert('!');
          this.phone = '';
          this.password = ''
        }
      }
    }
  }
</script>



