<template>
  <div>
    <h4></h4>
    <mt-field label="" placeholder="" type="tel" v-model="phone"></mt-field>
    <mt-field label="" placeholder="" type="password" v-model="password"></mt-field>
    <mt-field label="" placeholder="" type="password" v-model="pswd"></mt-field>
    <div @click="login" class="reg"></div>
  </div>
</template>
<style>

</style>
<script>
  import { MessageBox } from 'mint-ui';
  export default{
    data(){
      return{
        phone:'',
        password:'',
        pswd:''
      }},
    methods:{
      login(){
        if( !this.phone && !this.password && !this.pswd){
          MessageBox.alert('')
        }else if(this.password!=this.pswd ){
          MessageBox.alert('')
        }else  if(this.phone!=11){
          MessageBox.alert('11')
        }else{
          this.$store.state.people[this.phone] = this.password
          MessageBox.alert('')
          this.$router.push('/login')
        }
      }
    }
  }
</script>



